@import "../../global.scss";


.menu{
    width: 300px;
    height: 100vh;
    background-color: #1d2b30;
    position: fixed;
    top: 0;
    right: -300px;
    z-index: 2;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transition: all 1s ease;

    @include mobile {
        width: 50%;
        
    }

    &.active {
        right: 0;
    }

    ul {
        margin: 0;
        padding: 0;
        list-style: none;
        font-size: 30px;
        font-weight: 300;
        color: #758b8b;
        width: 60%;

    li {
        margin-bottom: 25px;
        a {
            font-size: inherit;
            color: inherit;
            text-decoration: none;
        }

        &:hover {
            font-weight: 500;
        }
    }

    }
}